@import '../../variables.scss';

header{
    background-color: $colorWhite;
    color: $colorTextDarkGrey;
    position: fixed;
    width: 100%;
    height: 80px;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 40px;
    z-index: 10;
    transition: .7s;
    &::after{
        width: 100%;
        height: 1px;
        background-color: #e4e4e4;
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
    }
    .header-logo{
        width: 120px;
        height: 40px;
        background-image: url('../../assets/img/logo.svg');
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
    }
    .header-nav{
        display: flex;
        align-items: center;
        justify-content: space-around;
        nav a{
            color: $colorTextDarkGrey;
            margin-right: 35px;
            transition: .3s;
            position: relative;
            &::after{
                width: 100%;
                height: 1.5px;
                background-color: $colorTextDarkGrey;
                content: "";
                position: absolute;
                bottom: -27px;
                left: 0;
                opacity: 0;
                transition: .7s;
            }
            &:hover {
                &::after{
                    opacity: 1;
                }
            }
        }
    }
    .header-user{
        display: flex;
        align-items: center;
        justify-content: space-around;
        position: relative;

        .user-account-div{
            .user-account-link{
                cursor: pointer;
                padding-right: 25px;
                margin-right: 35px;
                position: relative;
                &::after{
                    width: 20px;
                    height: 20px;
                    background-image: url('../../assets/img/icon-account.svg');
                    background-size: contain;
                    background-repeat: no-repeat;
                    content: "";
                    position: absolute;
                    top: 0;
                    right: 0;
                }
            }
        }
        .user-cart-div{
            .user-cart-link{
                padding-right: 25px;
                position: relative;
                &::after{
                    width: 20px;
                    height: 20px;
                    background-image: url('../../assets/img/icon-cart.svg');
                    background-size: contain;
                    background-repeat: no-repeat;
                    content: "";
                    position: absolute;
                    top: 0;
                    right: 0;
                }
            }
            .cart-quantity{
                position: absolute;
                top: -6px;
                right: -10px;
                width: 15px;
                height: 15px;
                background-color: $colorTextDarkGrey;
                border-radius: 50%;
                color: white;
                font-size: 10px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }
}
.header-fixed{
    width: 100%;
    height: 80px;
    z-index: 5;
    box-shadow: 0px 1px 12px rgba(0, 0, 0, 0.2);
}